  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

@font-face {
  font-family: "Exo2";
  src: url("../fonts/Exo20-Thin.woff2") format("woff2"), url("../fonts/Exo20-Thin.woff") format("woff");
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}

body {
  position: relative;
  min-width: 320px;
  font-size: 14px;
  font-weight: normal;
  color: #ffffff;
  line-height: 16px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  background-color: #000000;
  background-image: url("../img/bg.jpg");
  background-repeat: repeat;
  background-position: left top;
  background-size: 140px 140px;
}

body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent, rgba(0, 0, 0, 0.5));
}

img {
  width: 100%;
  height: auto;
}

p {
  margin: 0;
}

.page-main {
  min-height: 100vh;
}

.container {
  max-width: 1120px;
  box-sizing: border-box;
  margin: 0 auto;
}

@media (max-width: 1199px) {
  .container {
    max-width: 1023px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 320px;
  }
}

.cat-food {
  padding-top: 37px;
}

.cat-food h2 {
  margin: 0;
  font-family: "Exo2", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 200;
  line-height: normal;
  text-align: center;
  margin-bottom: 24px;
}

.cat-food__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.lav {
  color: #00c3ff;
  text-decoration: underline;
  cursor: pointer;
}

.product-list__item {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  position: relative;
  width: 320px;
  padding-top: 45px;
  margin-right: 80px;
}

.product-list__item:last-of-type {
  margin-right: 0;
}

.product-list__item:nth-of-type(3n) {
  margin-right: 0;
}

.product-list__item input {
  display: none;
}

@media (max-width: 1199px) {
  .product-list {
    justify-content: center;
  }
  .product-list__item {
    margin-bottom: 40px;
  }
  .product-list__item:nth-of-type(3n) {
    margin-right: 80px;
  }
  .product-list__item:nth-of-type(2n) {
    margin-right: 0;
  }
  .product-list__item:last-of-type {
    margin-right: 0;
  }
}

@media (max-width: 767px) {
  .product-list__item {
    margin-right: 0;
  }
  .product-list__item:nth-of-type(3n) {
    margin-right: 0;
  }
}

.item > p {
  text-align: center;
  font-size: 13px;
}

.item > p:not(:first-of-type) {
  display: none;
}

.item__wrapper:hover {
  border-color: #2ea8e5;
}

.item__wrapper:hover svg path:last-of-type {
  fill: #2ea8e5;
}

.item__wrapper:hover + p:first-of-type > label {
  color: #2ea8e5;
}

.item__wrapper:hover .item__weight {
  background-color: #2ea8e5;
}

.item label {
  color: #1698d9;
  border-bottom: 1px dashed;
  cursor: pointer;
}

.item label:hover {
  color: #2ea8e5;
}

.item input:checked + .item__wrapper {
  border-color: #d91667;
}

.item input:checked + .item__wrapper svg path:last-of-type {
  fill: #d91667;
}

.item input:checked + .item__wrapper .item__weight {
  background-color: #d91667;
}

.item input:checked + .item__wrapper + p {
  display: none;
}

.item input:checked + .item__wrapper + p + p {
  display: block;
}

.item input:disabled + .item__wrapper {
  border-color: #b3b3b3;
  background-image: url("../img/cat_hover.png");
  cursor: not-allowed;
}

.item input:disabled + .item__wrapper > .item__content p {
  color: rgba(179, 179, 179, 0.5);
}

.item input:disabled + .item__wrapper svg path:last-of-type {
  fill: #b3b3b3;
}

.item input:disabled + .item__wrapper .item__weight {
  background-color: #b3b3b3;
}

.item input:disabled + .item__wrapper + p {
  display: none;
}

.item input:disabled + .item__wrapper + p + p + p {
  display: block;
  color: #ffff66;
}

.item__wrapper {
  padding-left: 42px;
  padding-right: 12px;
  padding-bottom: 13px;
  width: 100%;
  min-height: 435px;
  background-color: #f2f2f2;
  border: 4px solid #1698d9;
  border-top: none;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  box-sizing: border-box;
  flex-shrink: 0;
  cursor: pointer;
  background-image: url("../img/cat.png");
  background-repeat: no-repeat;
  background-position: -27px 162px;
  margin-bottom: 13px;
}

.item__wrapper > svg {
  position: absolute;
  top: 1px;
  left: 0;
  height: 45px;
}

.item__content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.item__content > p {
  font-size: 16px;
  color: #666666;
  position: absolute;
  top: 1px;
  left: 48px;
  margin-top: 20px;
}

.item__content > p:last-of-type {
  color: #e62e7a;
}

.item__content-toggle {
  display: none;
}

.item__text {
  color: #666666;
}

.item__text > p {
  color: #000000;
  font-size: 24px;
  line-height: normal;
  font-weight: bold;
}

.item__text > p:first-of-type {
  font-size: 48px;
  word-break: break-word;
}

.item__desc {
  margin-top: 15px;
}

.item__desc span {
  font-weight: bold;
}

.item__weight {
  width: 80px;
  height: 80px;
  background-color: #1698d9;
  border-radius: 50%;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-self: flex-end;
  margin-top: auto;
}

.item__weight span:first-of-type {
  font-size: 42px;
  line-height: normal;
}

.item__weight span:last-of-type {
  font-size: 21px;
  line-height: 12px;
}
